:where(html) {
  color-scheme: light;
  --brand: var(--pink-6);
  --link: var(--indigo-7);
  --link-visited: var(--grape-7);
  --text-1: var(--gray-9);
  --text-2: var(--gray-7);
  --surface-1: var(--gray-0);
  --surface-2: var(--gray-2);
