}

@media (prefers-color-scheme: dark) {
  :where(html) {
    color-scheme: dark;
    --brand: var(--pink-4);
    --link: var(--indigo-3);
    --link-visited: var(--grape-3);
    --text-1: var(--gray-1);
    --text-2: var(--gray-2);
    --surface-1: var(--gray-9);
    --surface-2: var(--gray-8);
    --surface-3: var(--gray-7);
    --surface-4: var(--gray-6);
