/* New code by USWDS, adapted from Open Props */
html {
  scroll-behavior: smooth;
}

h1 {
  margin: 0;
}

.opener {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  background: white;
}

.hero {
  padding: var(--size-10);
  display: grid;
  gap: var(--size-5);
}

.hero-message {
  display: grid;
  grid-template-columns: max-content;
  color: var(--gray-9);
  line-height: var(--font-lineheight-0);
}

.hero-message > span {
  display: block;
}

.hero-message > span:last-child {
  color: var(--indigo-7);
}

.under-hero {
  color: var(--gray-7);
  font-size: var(--font-size-4);
  margin-block-end: var(--size-3);
}

.button-list {
  display: flex;
  gap: var(--size-3);
  margin-top: var(--size-3);
}

.button {
  background: white;
  color: var(--indigo-8);
  font-size: var(--font-size-3);
  padding-inline: var(--size-4);
  padding-block: var(--size-3);
  border-radius: var(--radius-2);
  box-shadow: var(--shadow-2);
  display: flex;
  align-items: center;
  line-height: 1;
}

.button span.right {
  padding-left: var(--size-2);
}

.button:hover {
  text-decoration: none;
}

.button.primary {
  background: var(--indigo-8);
  text-shadow: 0 1px 0 var(--indigo-9);
  color: white;
}

.button.primary:hover {
  background: var(--gray-9);
}

.button.secondary {
  background: white;
  border: var(--border-size-2) solid var(--indigo-8);
}

.button.secondary:visited {
  color: var(--indigo-8);
}

.button.secondary:hover {
  color: var(--gray-9);
  border-color: var(--gray-9);
}

.promo-art {
  align-self: stretch;
  background-color: var(--indigo-9);
}

.promo-art > img {
  block-size: 100%;
  object-fit: cover;
}

main {
  padding: 0;
  background-color: var(--yellow-7);
  color: var(--gray-9);
}

main > .part {
  border-left: var(--border-size-5) solid var(--yellow-7);
  padding: 0 var(--size-10) var(--size-10) var(--size-10);
  padding-left: calc(var(--size-10) - var(--border-size-5));
  counter-reset: steps-primary;
}

main > .part.intro {
  color: var(--indigo-4);
