}

main > .part.two {
  border-left-color: var(--teal-9);
}

main > .part.three {
  border-left-color: var(--teal-7);
}

main > .part.four {
  border-left-color: var(--teal-5);
}

main > .part.five {
  border-left-color: var(--teal-3);
}

main > .part.six {
  border-left-color: var(--teal-2);
}

main > .part.seven {
  border-left-color: var(--teal-1);
