  line-height: var(--font-lineheight-0);
  margin-top: 0;
  padding-top: var(--size-9);
  max-width: none;
}

main {
  --step-indent: 3rem;
}

main h3 {
  color: var(--gray-9);
  line-height: var(--font-lineheight-0);
  font-weight: normal;
  margin-top: var(--size-7);
  max-width: 64ex;
  margin-left: var(--step-indent);
  text-indent: calc(-1 * var(--step-indent));
}

.step:before {
  counter-increment: steps-primary;
  content: counter(steps-primary) ". ";
  font-weight: bold;
  display: inline-block;
  width: var(--step-indent);
  margin-left: 0;
  text-indent: 0;
}

.step-description {
  margin-left: var(--step-indent);
}

main p {
  margin-top: var(--size-3);
  font-size: var(--font-size-3);
  max-width: 80ex;
}

main p a[href],
main p a[href]:visited {
  color: var(--gray-9);
  text-decoration: underline;
  border-radius: var(--radius-1);
}

main p a:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

main p a:active {
  background-color: rgba(255, 255, 255, 0.85);
}

.subhed {
  font-size: var(--font-size-5);
  line-height: var(--font-lineheight-1);
  padding-top: var(--size-2);
}

.terminal,
.editor {
  font-size: var(--font-size-3);
  padding: var(--size-3);
  border-radius: var(--radius-2);
  margin-top: var(--size-4);
  margin-bottom: var(--size-4);
  position: relative;
}

.terminal {
  background-color: var(--gray-9);
  color: var(--gray-2);
}

.editor {
  background-color: rgba(255, 255, 255, 0.25);
  color: var(--gray-9);
  padding-top: var(--size-9);
  padding-bottom: var(--size-5);
}

.terminal pre,
.editor pre {
  padding: 0 var(--size-5);
  white-space: pre-wrap;
}

pre.blur {
  opacity: 0.5;
}

pre.feature {
  font-weight: bold;
}

.editor > .add,
.editor > .remove {
  text-indent: calc(-1 * var(--size-5));
}

.editor > .add {
  font-weight: bold;
}

.editor > .remove {
  color: var(--red-8);
}

.editor > .add:before,
.editor > .remove:before {
  width: var(--size-5);
  display: inline-flex;
  text-indent: 0;
  font-weight: normal;
}

.editor > .add:before {
  content: "+ ";
  color: var(--blue-8);
}

.editor > .remove:before {
  content: "- ";
  color: var(--red-8);
}

.editor .filename {
  font-family: var(--font-mono);
  font-weight: normal;
  font-size: var(--font-size-2);
  display: flex;
  position: absolute;
  padding: var(--size-2) var(--size-3) var(--size-2) var(--size-2);
  line-height: var(--font-lineheight-1);
  background: var(--blue-8);
  border-top-left-radius: var(--radius-2);
  top: 0;
  left: 0;
  align-items: center;
  max-width: 100%;
  color: white;
}

.editor .filename span {
  padding-right: 0.4em;
}

.terminal > pre:before {
  content: "> ";
  color: var(--red-8);
}

footer {
  font-size: var(--font-size-2);
  padding: var(--size-5) var(--size-10);
  background-color: var(--gray-9);
  color: white;
}

footer a {
  color: var(--blue-2);
}

footer a:hover {
  color: var(--blue-3);
}

footer a:visited {
  color: var(--indigo-3);
}

.alert {
  padding: var(--size-5);
  background: var(--yellow-1);
  border-radius: var(--radius-2);
}

.alert p:first-child {
  margin-top: 0;
}

ul.contents {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
  max-width: none;
  margin-top: var(--size-5);
}

ul.contents li {
  padding: 0;
  margin: 0;
  max-width: none;
}

a.contents-item {
  display: flex;
  padding: var(--size-3);
  align-items: center;
  border-left: var(--border-size-3) solid;
  width: 100%;
  margin-top: var(--size-2);
  font-size: var(--font-size-4);
  border-color: var(--indigo-7);
  color: white;
  margin-left: var(--size-1);
  line-height: var(--font-lineheight-1);
}

a.contents-item:hover {
  border-color: white;
  text-decoration: none;
  color: var(--yellow-7);
}

.contents-part {
  padding-right: var(--size-3);
  color: var(--indigo-4);
  font-weight: bold;
}

.contents-item .material-icons {
  margin-left: var(--size-1);
}